    export default {
        name: "address-list",
        data(){
            return {
                addresList: {},
                openid: "",
            }
        },
        created(){
            this.openid = localStorage.getItem('openid');
            this.getData();
        },
        methods:{
            getData(){
                var that = this;
                var formdata = new FormData();
                formdata.append('open_id', this.openid);
                that.axiosPost("/client/addr-list", formdata).then((res) => {
                    that.$vux.loading.hide();
                    if(res.status == 200){
                        that.addresList = res.data;
                    } else {
                        this.$vux.alert.show({
                            title: '温馨提示',
                            content: res.message});
                    }
                }, (err) => {
                    that.$vux.loading.hide();
                });
            },
            addAddr(){
                localStorage.setItem('address_edit', '');
                this.$router.push({path:'/addr/edit'});
            },
            editAddr(item){
                localStorage.setItem('address_edit', JSON.stringify(item));
                this.$router.push({path:'/addr/edit'});
            },
            selectAddr(item){
                var that = this;
                var formdata = new FormData();
                formdata.append('open_id', this.openid);
                formdata.append('aid', item.aid);
                that.axiosPost("/client/addr-default", formdata).then((res) => {
                    that.$vux.loading.hide();
                    if(res.status == 200){
                        that.addresList = res.data;
                        that.$router.push({path:'/order'});
                    } else {
                        this.$vux.alert.show({
                            title: '温馨提示',
                            content: res.message});
                    }
                }, (err) => {
                    that.$vux.loading.hide();
                });

            },
        }
    }



