    export default {
        name: "edit-address",
        data(){
            return {
                room_id : 0,
                openid: "",
                subStatus: 0,
                addrInfo: {
                    aid: 0,
                    name: "",
                    commmon: 0,
                    sex: 1,
                    mobile: "",
                    addr: "",
                    detail: "",
                },
            };
        },
        created(){
            this.openid = localStorage.getItem('openid');
            var address = localStorage.getItem('address_edit');
            if(address){
                this.addrInfo = JSON.parse(address);
            }
        },
        methods:{
            setSex(item, type){
                item.sex = type;
            },

            updateAddr(){
                var that = this;
                var addrInfo = this.addrInfo;
                if(this.subStatus){
                    return false;
                }

                if(!addrInfo.name){
                    that.$vux.alert.show({
                        title: '温馨提示',
                        content: "请填写联系人姓名"});
                    return false;
                }

                if (!addrInfo.mobile || (!(/^[1][34578]\d{9}$/).test(addrInfo.mobile) || !(/^[1-9]\d*$/).test(addrInfo.mobile) || addrInfo.mobile.length !== 11)) {
                    that.$vux.alert.show({
                        title: '温馨提示',
                        content: "请填写有效手机号"});
                    return false;
                }


                if(!addrInfo.addr){
                    that.$vux.alert.show({
                        title: '温馨提示',
                        content: "请填写收货地址"});
                    return false;
                }

                if(!addrInfo.detail){
                    that.$vux.alert.show({
                        title: '温馨提示',
                        content: "请填写门牌号"});
                    return false;
                }

                var formdata = new FormData();
                if(addrInfo.aid){
                    formdata.append('aid', addrInfo.aid);
                }

                formdata.append('client_name', addrInfo.name);
                formdata.append('commmon', addrInfo.commmon);
                formdata.append('client_sex', addrInfo.sex);
                formdata.append('mobile', addrInfo.mobile);
                formdata.append('addr', addrInfo.addr);
                formdata.append('detail', addrInfo.detail);
                formdata.append('openid', that.openid);

                that.$vux.loading.show({
                    text: '加载中~'
                });

                this.subStatus = true;
                that.axiosPost("/client/addr-save", formdata).then((res) => {
                    that.subStatus = false;
                    that.$vux.loading.hide();
                    if(res.status == 200){
                        that.$router.push({path:'/order'});
                    } else {
                        that.$vux.alert.show({
                            title: '温馨提示',
                            content: res.message});
                    }
                }, (err) => {
                    that.subStatus = false;
                    that.$vux.loading.hide();
                });
            },
        }
    }



