    export default {
        name: "comment",
        inject: ['reload'],
        props: ['room_id'],
        data(){
          return {
            commentList:[],
              total:0,
              roomid: 0,
              openid: "",

          };
        },
        created(){
            this.openid = localStorage.getItem('openid');
            this.getData();
        },
        methods:{
            getData(){
                var that = this;
                var formdata = new FormData();
                formdata.append('id', that.room_id);
                that.axiosPost("/room/comments", formdata).then((res) => {
                    that.$vux.loading.hide();
                    if(res.status == 200){
                        if(res.data.length > 0){
                            that.commentList =  res.data;
                            that.total = res.data.length;
                        }
                    } else {
                        this.$vux.alert.show({
                            title: '温馨提示',
                            content: res.message});
                    }
                }, (err) => {
                    that.$vux.loading.hide();
                });
            },

            setStart(item){
                var that = this;

                if(that.subStatus){
                    return false;
                }

                item.start = item.start == 1 ? 0 : 1;
                that.subStatus = true;

                var formdata = new FormData();
                formdata.append('openid', that.openid);
                formdata.append('id', item.id);
                formdata.append('stype', 2);

                that.axiosPost("/client/user-start", formdata).then((res) => {
                    that.subStatus = false;
                    if(res.status == 200){

                    } else {
                        that.$vux.alert.show({
                            title: '温馨提示',
                            content: res.message});
                    }
                }, (err) => {
                    that.subStatus = false;
                });
            },
            setComment(content){
                if(!content){
                    this.$vux.alert.show({
                        title: '温馨提示',
                        content: '请填写评论内容'});
                    return false;
                }

                var that = this;
                var formdata = new FormData();
                formdata.append('content', content);
                formdata.append('id', that.room_id);

                that.axiosPost("/client/comment", formdata).then((res) => {
                    that.$vux.loading.hide();
                    if(res.status == 200){
                        content = "";
                        //that.commentList.push(res.data);
                        that.$emit('clearComment', '')
                        that.reload();
                    } else {
                        that.$vux.alert.show({
                            title: '温馨提示',
                            content: res.message});
                    }
                }, (err) => {
                    that.$vux.loading.hide();
                });
            }
        }
    }



