    export default {
        name: 'my-order',
        data () {
            return {
                orderList: {}
            }
        },
        created () {
            this.getOrderList()
        },
        methods: {
            getOrderList () {
                let that = this
                var formdata = new FormData();
                that.axiosPost('/client/orders', formdata).then((res) => {
                    that.orderList = res.data
                }, (err) => {
                    console.log(err)
                })
            }
        }
    }



